


























import { Component, Prop, Vue } from "vue-property-decorator";
import { Chrome } from "vue-color";

@Component({
  components: {
    picker: Chrome
  }
})
export default class ColorPicker extends Vue {
  $refs!: {
    colorpicker: HTMLElement;
  };

  @Prop()
  value!: any;

  @Prop()
  placeholder!: string;

  @Prop({ default: false })
  hasAlpha!: boolean;

  private displayPicker: Boolean = false;
  private backgroundColor: String = "";

  colors: object = {};

  get alphaClass() {
    return this.hasAlpha
      ? this.colors["a"] === 1
        ? "nonAlpha"
        : "alpha"
      : false;
  }

  created() {
    this.colors = Object.assign({}, this.colors, { hex: this.value });
  }

  updateFromPicker(value: any) {
    this.colors = value;
    if (this.alphaClass === "alpha") {
      this.$emit("input", value.hex8);
    } else {
      this.$emit("input", value.hex);
    }
  }

  updateFromInput(event: any) {
    this.colors = event.target.value;
    this.$emit("input", event.target.value);
  }

  hidePicker() {
    document.removeEventListener("click", this.documentClick);
    this.displayPicker = false;
  }

  showPicker() {
    document.addEventListener("click", this.documentClick);
    this.displayPicker = true;
  }

  documentClick(e: any) {
    let el = this.$refs.colorpicker;
    let target = e.target;
    if (el !== target && !el.contains(target)) {
      this.hidePicker();
    }
  }
}
