















import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Radio extends Vue {
  @Prop(String)
  label!: string;

  @Prop(String)
  id!: string;

  @Prop(String)
  name!: string;

  @Prop([String, Boolean])
  value!: string | boolean;

  @Prop([String, Boolean])
  val!: string | boolean;
}
