


















import { Vue, Component, Prop } from "vue-property-decorator";

interface IOption {
  value: string;
  title: string;
  image: string;
}

@Component({})
export default class ImagePickerInput extends Vue {
  @Prop({ default: "above" })
  value!: string;

  @Prop(String)
  width!: string;

  @Prop(String)
  height!: string;

  @Prop({
    default: () => [
      {
        value: "above",
        title: "Above",
        image: "https://cdn.streamlabs.com/layouts/img/above.png"
      },
      {
        value: "banner",
        title: "Banner",
        image: "https://cdn.streamlabs.com/layouts/img/banner.png"
      },
      {
        value: "side",
        title: "Side",
        image: "https://cdn.streamlabs.com/layouts/img/side.png"
      }
    ]
  })
  options!: Array<IOption>;

  emitInput(val: string) {
    this.$emit("input", val);
  }
}
