



















































import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component({})
export default class BannerMarketing extends Vue {
  @Prop()
  bgImageNight!: {
    type: string;
  };

  @Prop()
  bgImage!: {
    type: string;
    required: true;
  };

  @Prop()
  label!: {
    type: string;
    required: true;
  };

  @Prop()
  iconName!: {
    type: string;
  };

  @Prop()
  iconImage!: {
    type: string;
  };

  @Prop()
  title!: {
    type: string;
    required: true;
  };

  @Prop()
  desc!: {
    type: string;
    required: false;
  };

  @Prop()
  linkDesc!: string;

  @Prop({ default: false })
  bannerClosed!: boolean;

  @Prop()
  onToggle!: Function;

  @Watch("bannerClosed")
  onBannerCloseStateChanged(val: boolean, oldVal: boolean) {
    this.closed = val;
    this.updateBannerHeight();
  }

  closed: boolean = false;

  mounted() {
    this.closed = this.bannerClosed;
    this.updateBannerHeight();
  }

  toggleBanner() {
    typeof this.onToggle === "function" && this.onToggle();
    this.closed = !this.closed;
    this.updateBannerHeight();
  }

  updateBannerHeight() {
    let banner: any = this.$refs.banner;
    let bannerWrapper: any = this.$refs.bottomWrapper;

    if (!this.closed) {
      banner.style.maxHeight = "240px";
    } else {
      setTimeout(() => {
        banner.style.maxHeight = `${bannerWrapper.scrollHeight + 32}px`;
      }, 1);
    }
  }
}
