





























import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./Button.vue";

@Component({
  components: {
    Button
  }
})
export default class TooltipNotice extends Vue {
  @Prop({ required: true })
  title!: string;

  @Prop({ default: "Got it" })
  buttonTitle!: string;

  @Prop({ default: "Learn More" })
  secondaryActionTitle!: string;

  @Prop({ required: true })
  desc!: string;

  @Prop({ default: "left" })
  arrowPosition!: string;

  @Prop({ default: true })
  hasButton!: boolean;

  @Prop({ default: false })
  hasSecondaryAction!: boolean;

  @Prop({ default: 200 })
  width!: number;

  clickHandler() {
    this.$emit("handle-tooltip");
  }

  secondaryClickHandler() {
    this.$emit("handle-tooltip-secondary");
  }

  get arrowClasses() {
    let classes: string[] = [];

    if (this.arrowPosition) {
      classes.push(`s-tooltip-notice__arrow--${this.arrowPosition}`);
    }

    return classes;
  }
}
