export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Forms</h1>\r\n      <p>\r\n        Wrap input components in form groups for different input and label\r\n        layouts.\r\n      </p>\r\n\r\n      <pre><code>import { FormGroup, FormGroupH, FormGroupV } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  FormGroup,\r\n  FormGroupH,\r\n  FormGroupV\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Form Group</h2>\r\n\r\n      <DemoSection title=\"Form Group\" :code=\"demoCode\">\r\n        <template #components>\r\n          <FormGroup>\r\n            <TextInput\r\n              slot=\"input\"\r\n              :type=\"textInputType\"\r\n              :placeholder=\"textInputPlaceholder\"\r\n              :label=\"title\"\r\n              v-model=\"inputValue\"\r\n            />\r\n          </FormGroup>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Horizontal Form Group</h2>\r\n\r\n      <DemoSection title=\"Horizontal Form Group\" :code=\"demoCode\">\r\n        <template #components>\r\n          <FormGroupH :title=\"title\" :helpText=\"helpText\" :tooltip=\"tooltip\">\r\n            <TextInput\r\n              slot=\"input\"\r\n              :type=\"textInputType\"\r\n              :placeholder=\"textInputPlaceholder\"\r\n              v-model=\"inputValue\"\r\n            />\r\n          </FormGroupH>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Vertical Form Group</h2>\r\n\r\n      <DemoSection title=\"Vertical Form Group\" :code=\"demoCode\">\r\n        <template #components>\r\n          <FormGroupV\r\n            :title=\"title\"\r\n            label=\"Vertical Form Group Input\"\r\n            titleLayout=\"flex-start\"\r\n            :tooltip=\"tooltip\"\r\n            :helpText=\"helpText\"\r\n          >\r\n            <TextInput\r\n              slot=\"input\"\r\n              :type=\"textInputType\"\r\n              :placeholder=\"textInputPlaceholder\"\r\n              v-model=\"inputValue\"\r\n            />\r\n          </FormGroupV>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <!-- <div class=\"section\">\r\n      <h2>Payment Form</h2>\r\n\r\n      <DemoSection title=\"Payment Form\" :code=\"demoCode\">\r\n        <template #components>\r\n          <PaymentForm />\r\n        </template>\r\n      </DemoSection>\r\n    </div>-->\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport FormsCode from \"!!raw-loader!./Forms.vue\";\r\nimport FormGroup from \"./../components/FormGroup.vue\";\r\nimport FormGroupH from \"./../components/FormGroupH.vue\";\r\nimport FormGroupV from \"./../components/FormGroupV.vue\";\r\nimport PaymentForm from \"./../components/PaymentForm.vue\";\r\nimport TextInput from \"./../components/TextInput.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    FormGroup,\r\n    FormGroupH,\r\n    FormGroupV,\r\n    PaymentForm,\r\n    TextInput\r\n  }\r\n})\r\nexport default class Forms extends Vue {\r\n  demoCode = FormsCode;\r\n  title = \"Text Input\";\r\n  tooltip = \"Input tooltip message.\";\r\n  helpText = \"This is help text\";\r\n  textInputPlaceholder = \"Text input placeholder\";\r\n  textInputType = \"text\";\r\n  inputValue = \"\";\r\n}\r\n</script>\r\n";