

























































import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./../components/Button.vue";
import VueMq from "vue-mq";
import VModal from "vue-js-modal";

Vue.use(VModal);

Vue.use(VueMq, {
  breakpoints: {
    // default breakpoints - customize this
    sm: 900,
    md: 1250,
    lg: Infinity
  },
  defaultBreakpoint: "sm" // customize this for SSR
});

@Component({
  components: {
    Button
  }
})
export default class NewFeatureOverlay extends Vue {
  @Prop({ default: "100%" })
  width!: string | number;

  @Prop({ default: "auto" })
  height!: string | number;

  @Prop()
  label!: string;

  @Prop()
  title!: string;

  @Prop()
  media!: string;

  @Prop()
  buttonTitle!: string;

  @Prop({ default: "/" })
  buttonRoute!: string;

  @Prop({ default: "router-link" })
  buttonTag!: String;

  @Prop()
  buttonHref!: String;

  @Prop()
  buttonTarget!: String;

  @Prop({ default: "/" })
  dismissRoute!: string;

  @Prop({ default: "Go to Dashboard" })
  dismissText!: string;

  @Prop()
  onOpen!: Function;

  @Prop()
  onAction!: Function;

  @Prop({ default: false })
  videoControls!: boolean;

  isImage: boolean = true;

  get overlayImage() {
    return this.media;
  }

  $mq!: string | string[];

  get containerMq() {
    return this.$mq === "sm" ? "s-overlay__container--mq" : "";
  }

  get overlay__imageBlockMq() {
    return this.$mq === "sm" ? "s-overlay__image-block--mq" : "";
  }

  mounted() {
    if (this.media.includes("mp4") || this.media.includes("webm")) {
      this.isImage = false;
    } else {
      this.isImage = true;
    }
  }

  opened(event) {
    typeof this.onOpen === "function" && this.onOpen();
  }

  onPrimaryAction() {
    typeof this.onAction === "function" && this.onAction();
    this.onDismiss();
  }

  onDismiss() {
    this.$modal.hide("new-feature");
  }
}
