






















import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./../components/Button.vue";
import Spinner from "./../components/Spinner.vue";

@Component({
  components: {
    Button,
    Spinner
  }
})
export default class ModalRedirect extends Vue {
  @Prop()
  name!: string;

  @Prop({ default: 600 })
  width!: number;

  @Prop({ default: 600 })
  minWidth!: number;

  @Prop()
  title!: string;

  @Prop()
  text!: string;
}
