export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Notices</h1>\r\n      <p>This is used to point out new locations or features</p>\r\n    </div>\r\n    <div class=\"section\">\r\n      <h2>Tooltip Notice</h2>\r\n\r\n      <DemoSection title=\"Default\" :code=\"demoCode\">\r\n        <template #components>\r\n          <TooltipNotice\r\n            v-if=\"showTooltipNotice\"\r\n            title=\"Defaults\"\r\n            desc=\"All your favorite widgets, just with a new view.\"\r\n            @handle-tooltip=\"closeTooltip\"\r\n          />\r\n\r\n          <TooltipNotice\r\n            v-if=\"showTooltipNotice\"\r\n            title=\"Top arrow\"\r\n            desc=\"All your favorite widgets, just with a new view.\"\r\n            :arrowPosition=\"'top'\"\r\n            :hasButton=\"false\"\r\n            @handle-tooltip=\"closeTooltip\"\r\n          />\r\n\r\n          <TooltipNotice\r\n            v-if=\"showTooltipNotice\"\r\n            title=\"Custom button title\"\r\n            desc=\"All your favorite widgets, just with a new view.\"\r\n            :buttonTitle=\"'Get started'\"\r\n            @handle-tooltip=\"closeTooltip\"\r\n          />\r\n\r\n          <TooltipNotice\r\n            v-if=\"showTooltipNotice\"\r\n            title=\"Custom width\"\r\n            desc=\"All your favorite widgets, just with a new view.\"\r\n            :width=\"300\"\r\n            @handle-tooltip=\"closeTooltip\"\r\n          />\r\n\r\n          <TooltipNotice\r\n            v-if=\"showTooltipNotice\"\r\n            title=\"Secondary Action\"\r\n            desc=\"All your favorite widgets, just with a new view.\"\r\n            :hasSecondaryAction=\"true\"\r\n            secondaryActionTitle=\"No thanks\"\r\n            @handle-tooltip=\"alertTooltip\"\r\n            @handle-tooltip-secondary=\"closeTooltip\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Props</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>arrowPosition</td>\r\n            <td>string</td>\r\n            <td>'left'</td>\r\n            <td>\r\n              Side to put the arrow on. Options are\r\n              <code>left</code> and\r\n              <code>top</code>\r\n            </td>\r\n          </tr>\r\n\r\n          <tr>\r\n            <td>buttonTitle</td>\r\n            <td>string</td>\r\n            <td>'Got It'</td>\r\n            <td>Button text</td>\r\n          </tr>\r\n\r\n          <tr>\r\n            <td>hasButton</td>\r\n            <td>boolean</td>\r\n            <td>true</td>\r\n            <td>Having a button is optional</td>\r\n          </tr>\r\n\r\n          <tr>\r\n            <td>hasSecondaryAction</td>\r\n            <td>boolean</td>\r\n            <td>false</td>\r\n            <td>Have a second action option</td>\r\n          </tr>\r\n\r\n          <tr>\r\n            <td>secondaryActionTitle</td>\r\n            <td>string</td>\r\n            <td>'Learn more'</td>\r\n            <td>Title for secondary action</td>\r\n          </tr>\r\n\r\n          <tr>\r\n            <td>width</td>\r\n            <td>number</td>\r\n            <td>200</td>\r\n            <td>Change the width of the tooltip</td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport NoticesCode from \"!!raw-loader!./Notices.vue\";\r\nimport TooltipNotice from \"./../components/TooltipNotice.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    TooltipNotice\r\n  }\r\n})\r\nexport default class Notices extends Vue {\r\n  demoCode = NoticesCode;\r\n  showTooltipNotice = true;\r\n\r\n  alertTooltip() {\r\n    window.alert(\"tooltip clicked\");\r\n  }\r\n\r\n  closeTooltip() {\r\n    this.showTooltipNotice = false;\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n::v-deep .s-demo-section__content {\r\n  height: 330px;\r\n\r\n  .s-tooltip-notice {\r\n    &:nth-child(1) {\r\n      top: 0px;\r\n      left: 0px;\r\n    }\r\n\r\n    &:nth-child(2) {\r\n      top: 0;\r\n      left: 250px;\r\n    }\r\n\r\n    &:nth-child(3) {\r\n      top: 0;\r\n      left: 500px;\r\n    }\r\n\r\n    &:nth-child(4) {\r\n      top: 169px;\r\n      left: 0;\r\n    }\r\n\r\n    &:nth-child(5) {\r\n      top: 169px;\r\n      left: 350px;\r\n    }\r\n  }\r\n}\r\n</style>\r\n";