export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Media Pickers</h1>\r\n\r\n      <pre><code>import { MediaPicker } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  MediaPicker\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <DemoSection title=\"Default\" :code=\"demoCode\">\r\n        <template #components>\r\n          <FormGroup>\r\n            <media-picker\r\n              slot=\"input\"\r\n              variation=\"image\"\r\n              :media-link=\"true\"\r\n              @select-media=\"selectImageMedia\"\r\n              @preview-media=\"previewImageMedia\"\r\n              @remove-media=\"removeImageMedia\"\r\n              v-model=\"imageMedia\"\r\n            />\r\n\r\n            <media-picker\r\n              slot=\"input\"\r\n              variation=\"audio\"\r\n              :media-link=\"true\"\r\n              @select-media=\"selectAudioMedia\"\r\n              @remove-media=\"removeAudioMedia\"\r\n              v-model=\"audioMedia\"\r\n            />\r\n          </FormGroup>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Props</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>variation</td>\r\n            <td>string</td>\r\n            <td>null</td>\r\n            <td>\r\n              Available variations include `image` and `audio`. Video is under\r\n              the `image` variation.\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>mediaLink</td>\r\n            <td>boolean</td>\r\n            <td>false</td>\r\n            <td>\r\n              Will display a clickable link icon that emits `link-media` event.\r\n              Primarily used on SL core to add a link to a media href.\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>value</td>\r\n            <td>string</td>\r\n            <td>null</td>\r\n            <td>A URL string to the selected media.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>link-media</td>\r\n            <td>event</td>\r\n            <td>null</td>\r\n            <td>Event emitted when the link icon is clicked</td>\r\n          </tr>\r\n          <tr>\r\n            <td>preview-media</td>\r\n            <td>event</td>\r\n            <td>null</td>\r\n            <td>\r\n              Event emitted when the magnifiy glass or play icons are clicked\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>remove-media</td>\r\n            <td>event</td>\r\n            <td>null</td>\r\n            <td>\r\n              Event emitted when the close icon or 'Remove' button is clicked\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>select-media</td>\r\n            <td>event</td>\r\n            <td>null</td>\r\n            <td>\r\n              Event emitted when the plus icon or 'Select Media' button is\r\n              clicked\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport FormGroup from \"./../components/FormGroup.vue\";\r\nimport MediaPicker from \"./../components/MediaPicker.vue\";\r\nimport MediaPickersCode from \"!!raw-loader!./MediaPickers\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    FormGroup,\r\n    MediaPicker\r\n  }\r\n})\r\nexport default class ImagePickers extends Vue {\r\n  demoCode = MediaPickersCode;\r\n  msg = \"Hi!\";\r\n  audioMedia = \"\";\r\n  imageMedia = \"\";\r\n\r\n  selectImageMedia() {\r\n    this.imageMedia =\r\n      \"https://uploads.twitchalerts.com/000/045/005/127/foolofsoul-design-1521842129-0.png\";\r\n  }\r\n\r\n  selectAudioMedia() {\r\n    this.audioMedia =\r\n      \"https://cdn1.twitchalerts.com/twitch-bits/sounds/bits.ogg\";\r\n  }\r\n\r\n  removeImageMedia() {\r\n    this.imageMedia = \"\";\r\n  }\r\n\r\n  removeAudioMedia() {\r\n    this.audioMedia = \"\";\r\n  }\r\n\r\n  previewImageMedia() {\r\n    window.open(this.imageMedia);\r\n  }\r\n}\r\n</script>\r\n";