export default "<template>\r\n  <div>\r\n    <h1>Banners</h1>\r\n    <Tabs :tabs=\"tabs\" :value=\"selectedTab\" @input=\"onSelectTabHandler\">\r\n      <div slot=\"marketing\">\r\n        <h2>Marketing Banner</h2>\r\n        <DemoSection title=\"Marketing Banner\" :code=\"demoCode\">\r\n          <template #components>\r\n            <banner-marketing\r\n              bg-image=\"\r\n                https://cdn.streamlabs.com/static/imgs/pretzel_dashboard_banner_bg.png\r\n              \"\r\n              bg-image-night=\"\r\n                http://cdn.backgroundhost.com/backgrounds/subtlepatterns/cartographer.png\r\n              \"\r\n              icon-name=\"themes\"\r\n              label=\"Introducing Streamlabs OBS\"\r\n              title=\"Largest library of free themes in the world.\"\r\n              desc=\"\r\n                To access over 700+ themes for free, download Streamlabs OBS.\r\n              \"\r\n              link-desc=\"Win 7+  245.8 MB\"\r\n              :onToggle=\"test\"\r\n            >\r\n              <Button\r\n                slot=\"link\"\r\n                type=\"a\"\r\n                variation=\"action\"\r\n                size=\"standard\"\r\n                href=\"#\"\r\n                title=\"Download\"\r\n                icon=\"overview\"\r\n              />\r\n            </banner-marketing>\r\n          </template>\r\n        </DemoSection>\r\n\r\n        <banner-marketing\r\n          bg-image=\"\r\n            https://cdn.streamlabs.com/static/imgs/pretzel_dashboard_banner_bg.png\r\n          \"\r\n          icon-image=\"require(../assets/imgs/pretzel-icon.png)\"\r\n          label=\"New Streamlabs OBS App\"\r\n          title=\"Introducing Pretzel Rocks Music Player\"\r\n          desc=\"\r\n            Stream-safe music for broadcasters with an ever-growing catalog of music. Currently 5k+ tracks.\r\n          \"\r\n          link-desc=\"$4.99/mo\"\r\n          :banner-closed=\"bannerClosed\"\r\n        >\r\n          <Button\r\n            slot=\"link\"\r\n            type=\"a\"\r\n            variation=\"action\"\r\n            size=\"standard\"\r\n            href=\"#\"\r\n            title=\"Try It Now\"\r\n          />\r\n        </banner-marketing>\r\n\r\n        <table class=\"docs-table\">\r\n          <thead>\r\n            <tr>\r\n              <th>Prop</th>\r\n              <th>Type</th>\r\n              <th>Required</th>\r\n              <th>Default</th>\r\n              <th>Description</th>\r\n            </tr>\r\n          </thead>\r\n          <tbody>\r\n            <tr>\r\n              <td>bgImage</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Background image that will display in day mode.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>bgImageNight</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>null</td>\r\n              <td>Background image that will display in night mode.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>label</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Label text in the upper left of the banner.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>iconImage</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>null</td>\r\n              <td>\r\n                Icon image next to title. Use a url path. If using iconName - do\r\n                not use iconImg. We will improve functionality in near future.\r\n              </td>\r\n            </tr>\r\n            <tr>\r\n              <td>iconName</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>null</td>\r\n              <td>\r\n                Icon next to title. Use icon name from icon list. If using\r\n                iconImg - do not use iconName. We will improve functionality in\r\n                near future.\r\n              </td>\r\n            </tr>\r\n            <tr>\r\n              <td>title</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Main banner title.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>desc</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Description text below banner title.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>linkDesc</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>null</td>\r\n              <td>Description text below link.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>bannerClosed</td>\r\n              <td>boolean</td>\r\n              <td>false</td>\r\n              <td>false</td>\r\n              <td>Default state the banner is. Open by default.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>onToggle</td>\r\n              <td>function</td>\r\n              <td>null</td>\r\n              <td>false</td>\r\n              <td>Function which is called when you toggle the icon close.</td>\r\n            </tr>\r\n          </tbody>\r\n        </table>\r\n      </div>\r\n\r\n      <div slot=\"sale\">\r\n        <h2>Sale Banner</h2>\r\n        <DemoSection title=\"Sale Banner\" :code=\"demoCode\">\r\n          <template #components>\r\n            <banner-sale\r\n              title=\"Flash Sale! 25% off everything!\"\r\n              desc=\"All items are automatically discounted.\"\r\n              days=\"00\"\r\n              hours=\"00\"\r\n              minutes=\"00\"\r\n              :seconds=\"secs\"\r\n              time-desc=\"Until flash sale ends. Hurry!\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n\r\n        <table class=\"docs-table\">\r\n          <thead>\r\n            <tr>\r\n              <th>Prop</th>\r\n              <th>Type</th>\r\n              <th>Required</th>\r\n              <th>Default</th>\r\n              <th>Description</th>\r\n            </tr>\r\n          </thead>\r\n          <tbody>\r\n            <tr>\r\n              <td>title</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Banner main title.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>desc</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Banner description text below main title.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>days</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>\"00\"</td>\r\n              <td>Days remaining value for countdown timer</td>\r\n            </tr>\r\n            <tr>\r\n              <td>hours</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>\"00\"</td>\r\n              <td>Hours remaining value for countdown timer</td>\r\n            </tr>\r\n            <tr>\r\n              <td>minutes</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>\"00\"</td>\r\n              <td>Minutes remaining value for countdown timer</td>\r\n            </tr>\r\n            <tr>\r\n              <td>seconds</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>\"00\"</td>\r\n              <td>Seconds remaining value for countdown timer</td>\r\n            </tr>\r\n            <tr>\r\n              <td>timeDesc</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>null</td>\r\n              <td>Description text below countdown timer.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>borderColor</td>\r\n              <td>string</td>\r\n              <td>false</td>\r\n              <td>\"rgba(248, 86, 64, 0.33)\"</td>\r\n              <td>Banner border color.</td>\r\n            </tr>\r\n            <tr>\r\n              <td>backgroundColor</td>\r\n              <td>boolean</td>\r\n              <td>false</td>\r\n              <td>\"rgba(248, 86, 64, 0.08)\"</td>\r\n              <td>Banner background color.</td>\r\n            </tr>\r\n          </tbody>\r\n        </table>\r\n      </div>\r\n\r\n      <div slot=\"notice\">\r\n        <h2>Notice Banner</h2>\r\n        <DemoSection title=\"Notice Banner\" :code=\"demoCode\">\r\n          <template #components>\r\n            <notice\r\n              title=\"\r\n                Join affiliates and earn $1 for each Streamlabs OBS referral\r\n              \"\r\n              desc=\"\r\n                Share your unique referral link with friends and get paid directly into your PayPal each month.\r\n              \"\r\n              icon=\"information\"\r\n            >\r\n              <Button\r\n                slot=\"button\"\r\n                type=\"button\"\r\n                size=\"fixed-width\"\r\n                variation=\"action\"\r\n                title=\"Join\"\r\n              />\r\n            </notice>\r\n          </template>\r\n        </DemoSection>\r\n        <br />\r\n        <br />\r\n\r\n        <notice\r\n          :bgColor=\"'warning'\"\r\n          :title=\"'Your donation link has expired'\"\r\n          :titleColor=\"'warning'\"\r\n          :desc=\"\r\n            'Copy your new donation link and replace all instances containing twitchalerts.com.'\r\n          \"\r\n          :icon=\"'error'\"\r\n        >\r\n          <Button\r\n            slot=\"button\"\r\n            :type=\"'button'\"\r\n            :size=\"'fixed-width'\"\r\n            :variation=\"'default'\"\r\n            :title=\"'Copy Link'\"\r\n          ></Button>\r\n        </notice>\r\n\r\n        <table class=\"docs-table\">\r\n          <thead>\r\n            <tr>\r\n              <th>Prop</th>\r\n              <th>Type</th>\r\n              <th>Required</th>\r\n              <th>Default</th>\r\n              <th>Description</th>\r\n            </tr>\r\n          </thead>\r\n          <tbody>\r\n            <tr>\r\n              <td>bgColor</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>default</td>\r\n              <td>\r\n                Background color that will display. Options are \"default\" and\r\n                \"warning\". if default It's already set as default props.\r\n              </td>\r\n            </tr>\r\n            <tr>\r\n              <td>title</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Banner title</td>\r\n            </tr>\r\n            <tr>\r\n              <td>titleColor</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Banner title color</td>\r\n            </tr>\r\n            <tr>\r\n              <td>desc</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Description text below banner title</td>\r\n            </tr>\r\n            <tr>\r\n              <td>icon</td>\r\n              <td>string</td>\r\n              <td>true</td>\r\n              <td>null</td>\r\n              <td>Icon next to title. Use icon name from icon list.</td>\r\n            </tr>\r\n          </tbody>\r\n        </table>\r\n      </div>\r\n\r\n      <div slot=\"discord\">\r\n        <h2>Discord Banner</h2>\r\n        <DemoSection title=\"Discord Banner\" :code=\"demoCode\">\r\n          <template #components>\r\n            <BannerDiscord />\r\n          </template>\r\n        </DemoSection>\r\n\r\n        <table class=\"docs-table\">\r\n          <thead>\r\n            <tr>\r\n              <th>Prop</th>\r\n              <th>Type</th>\r\n              <th>Default</th>\r\n              <th>Description</th>\r\n            </tr>\r\n          </thead>\r\n          <tbody>\r\n            <tr>\r\n              <td>title</td>\r\n              <td>string</td>\r\n              <td>\r\n                Join the Streamlabs OBS Discussion on\r\n                &lt;span&gt;Discord&lt;/span&gt;\r\n              </td>\r\n              <td>\r\n                Set the title of the banner. You can wrap text in\r\n                <code>&lt;span&gt;</code> tags to give them medium font weight.\r\n              </td>\r\n            </tr>\r\n          </tbody>\r\n        </table>\r\n      </div>\r\n    </Tabs>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\nimport BannersCode from \"!!raw-loader!./Banners.vue\";\r\nimport BannerDiscord from \"./../components/BannerDiscord.vue\";\r\nimport BannerMarketing from \"./../components/BannerMarketing.vue\";\r\nimport BannerSale from \"./../components/BannerSale.vue\";\r\nimport Button from \"./../components/Button.vue\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport Notice from \"./../components/Notice.vue\";\r\nimport Tabs from \"./../components/Tabs.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    BannerDiscord,\r\n    BannerMarketing,\r\n    BannerSale,\r\n    Button,\r\n    DemoSection,\r\n    Notice,\r\n    Tabs\r\n  }\r\n})\r\nexport default class Banners extends Vue {\r\n  demoCode = BannersCode;\r\n  tabs = [\r\n    {\r\n      name: \"Marketing\",\r\n      value: \"marketing\"\r\n    },\r\n    {\r\n      name: \"Sale\",\r\n      value: \"sale\"\r\n    },\r\n    {\r\n      name: \"Notice\",\r\n      value: \"notice\"\r\n    },\r\n    {\r\n      name: \"Discord\",\r\n      value: \"discord\"\r\n    }\r\n  ];\r\n\r\n  selectedTab = \"marketing\";\r\n  remainingSecs = 10;\r\n\r\n  bannerClosed = false;\r\n\r\n  mounted() {\r\n    setInterval(() => {\r\n      this.remainingSecs--;\r\n      if (this.remainingSecs < 0) {\r\n        this.remainingSecs = 10;\r\n      }\r\n    }, 1000);\r\n  }\r\n\r\n  onSelectTabHandler(tab: string) {\r\n    this.selectedTab = tab;\r\n  }\r\n\r\n  get secs() {\r\n    return this.remainingSecs < 10\r\n      ? `0${this.remainingSecs}`\r\n      : `${this.remainingSecs}`;\r\n  }\r\n\r\n  test() {\r\n    console.log(\"test\");\r\n  }\r\n}\r\n</script>\r\n";