

























import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./../components/Button.vue";
import VueMq from "vue-mq";

Vue.use(VueMq, {
  breakpoints: {
    // default breakpoints - customize this
    sm: 900,
    md: 1250,
    lg: Infinity
  },
  defaultBreakpoint: "sm" // customize this for SSR
});

@Component({
  components: {
    Button
  }
})
export default class ContentRow extends Vue {
  @Prop()
  icon!: string;

  @Prop({ default: "default" })
  btnVariation!: string;

  @Prop({ default: "Default" })
  btnTitle!: string;

  @Prop()
  buttonHref!: String;

  @Prop()
  buttonTo!: String;

  @Prop({ default: "button" })
  buttonTag!: String;

  $mq: any;

  get contentRowMq() {
    return this.$mq === "sm" ? "s-content-row-mq" : "";
  }

  get contentBoxMq() {
    return this.$mq === "sm" ? "s-content-box-mq" : "";
  }

  get bannerIconMq() {
    return this.$mq === "sm" ? "s-banner__icon-mq" : "";
  }

  get contentTitleMq() {
    return this.$mq === "sm" ? "s-content__title-mq" : "";
  }

  get contentTextMq() {
    return this.$mq === "sm" ? "s-content__text-mq" : "";
  }
}
