





















import { Component, Prop, Vue } from "vue-property-decorator";
import Badge from "./../components/Badge.vue";

@Component({
  components: {
    Badge
  }
})
export default class Step extends Vue {
  @Prop(String)
  title!: string;

  @Prop(String)
  icon!: string;

  @Prop({ default: false })
  isCompleted!: boolean;

  @Prop(String)
  completedText!: string;

  @Prop({ default: false })
  hasCheckmark!: boolean;

  @Prop({ default: false })
  hasPrime!: boolean;
}
