













import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Checkbox extends Vue {
  @Prop({ type: String, required: true })
  label!: string;

  @Prop({ type: String, required: true })
  id!: string;

  @Prop({ type: String })
  name!: string;

  @Prop({ type: Boolean, required: true })
  value!: boolean;
}
