export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Buttons</h1>\r\n      <p>\r\n        Our button component is super flexible. It can act as type\r\n        <code>button</code>, <code>a</code> tag or <code>router-link</code>.\r\n        Please note that you may need to use 'ButtonInput' rather than 'Button'\r\n        to avoid issues.\r\n      </p>\r\n\r\n      <pre><code>import { Button } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  Button\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Standard Buttons</h2>\r\n      <p>Used throughout website Dashboard.</p>\r\n      <div class=\"section\">\r\n        <h3>Active</h3>\r\n        <DemoSection title=\"Active\" :code=\"demoCode\">\r\n          <template #components>\r\n            <div class=\"s-button-container s-button-container--left\">\r\n              <Button\r\n                variation=\"default\"\r\n                title=\"Default\"\r\n                tag=\"a\"\r\n                href=\"https://laravel.com/docs/5.8/routing\"\r\n                @click=\"reportSlobsDownloads\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"action\"\r\n                title=\"Action\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"warning\"\r\n                title=\"Warning\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"default\"\r\n                title=\"with icon\"\r\n                icon=\"add-circle\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"default\"\r\n                title=\"Custom Colors\"\r\n                bg-color=\"pink\"\r\n                text-color=\"red\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"prime\"\r\n                title=\"Join Prime\"\r\n                icon=\"prime\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button\r\n                variation=\"prime-white\"\r\n                title=\"Join Prime\"\r\n                icon=\"prime\"\r\n                @click=\"buttonActionClick\"\r\n              />\r\n\r\n              <Button variation=\"action\">\r\n                <div slot=\"custom\" class=\"custom-html\">\r\n                  <i class=\"icon-add-circle\"></i>\r\n                  <span>Custom Slot</span>\r\n                </div>\r\n              </Button>\r\n            </div>\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Disabled</h3>\r\n        <DemoSection title=\"Disabled\" :code=\"demoCode\">\r\n          <template #components>\r\n            <div class=\"s-button-container s-button-container--left\">\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'disabled'\"\r\n                :title=\"'Default'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'action'\"\r\n                :state=\"'disabled'\"\r\n                :title=\"'Action'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'warning'\"\r\n                :state=\"'disabled'\"\r\n                :title=\"'Warning'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'disabled'\"\r\n                :title=\"'with icon'\"\r\n                :icon=\"'edit'\"\r\n              />\r\n            </div>\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Focus</h3>\r\n        <DemoSection title=\"Focus\" :code=\"demoCode\">\r\n          <template #components>\r\n            <div class=\"s-button-container s-button-container--left\">\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'focused'\"\r\n                :title=\"'Default'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'action'\"\r\n                :state=\"'focused'\"\r\n                :title=\"'Action'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'warning'\"\r\n                :state=\"'focused'\"\r\n                :title=\"'Warning'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'focused'\"\r\n                :title=\"'with icon'\"\r\n                :icon=\"'edit'\"\r\n              />\r\n            </div>\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Loading</h3>\r\n        <DemoSection title=\"Loading\" :code=\"demoCode\">\r\n          <template #components>\r\n            <div class=\"s-button-container s-button-container--left\">\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'loading'\"\r\n                :title=\"'Default'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'action'\"\r\n                :state=\"'loading'\"\r\n                :title=\"'Action'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'warning'\"\r\n                :state=\"'loading'\"\r\n                :title=\"'Warning'\"\r\n              />\r\n\r\n              <Button\r\n                :variation=\"'default'\"\r\n                :state=\"'loading'\"\r\n                :title=\"'with icon'\"\r\n                :icon=\"'recent-events'\"\r\n              />\r\n            </div>\r\n          </template>\r\n        </DemoSection>\r\n\r\n        <p>\r\n          Click\r\n          <a @click=\"isLoadingExample = !isLoadingExample\">here</a> to test.\r\n        </p>\r\n\r\n        <Button\r\n          :class=\"{ 'is-loading': isLoadingExample }\"\r\n          :type=\"'button'\"\r\n          :variation=\"'default'\"\r\n          :title=\"'with icon'\"\r\n          :icon=\"'pop-out-1'\"\r\n        />\r\n      </div>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Small Buttons</h2>\r\n      <p>\r\n        Small is the size used on Streamlabs OBS. It is slightly smaller than\r\n        our standard button.\r\n      </p>\r\n\r\n      <DemoSection title=\"Small Buttons\" :code=\"demoCode\">\r\n        <template #components>\r\n          <div class=\"s-button-container s-button-container--left\">\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'small'\"\r\n              :variation=\"'default'\"\r\n              :title=\"'Default'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'small'\"\r\n              :variation=\"'action'\"\r\n              :title=\"'Action'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'small'\"\r\n              :variation=\"'warning'\"\r\n              :title=\"'Warning'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'small'\"\r\n              :variation=\"'default'\"\r\n              :title=\"'With Icon'\"\r\n              :icon=\"'image'\"\r\n            />\r\n\r\n            <Button\r\n              :variation=\"'rewards-standard'\"\r\n              :size=\"'small'\"\r\n              :title=\"'Rewards'\"\r\n            />\r\n          </div>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Large Buttons</h2>\r\n      <p>\r\n        Used on website marketing pages and for the Donate button on the tip\r\n        page.\r\n      </p>\r\n      <DemoSection title=\"Large Buttons\" :code=\"demoCode\">\r\n        <template #components>\r\n          <div class=\"s-button-container s-button-container--left\">\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'large'\"\r\n              :variation=\"'default'\"\r\n              :title=\"'Default'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'large'\"\r\n              :variation=\"'action'\"\r\n              :title=\"'Action'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'large'\"\r\n              :variation=\"'warning'\"\r\n              :title=\"'Warning'\"\r\n            />\r\n\r\n            <Button\r\n              :type=\"'button'\"\r\n              :size=\"'large'\"\r\n              :variation=\"'default'\"\r\n              :title=\"'With Icon'\"\r\n              :icon=\"'image'\"\r\n            />\r\n\r\n            <Button\r\n              :size=\"'large'\"\r\n              :variation=\"'prime'\"\r\n              :title=\"'Join Prime'\"\r\n              :icon=\"'prime'\"\r\n            />\r\n            <Button\r\n              :size=\"'large'\"\r\n              :variation=\"'prime-white'\"\r\n              :title=\"'Join Prime'\"\r\n              :icon=\"'prime'\"\r\n            />\r\n          </div>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Fixed Width Buttons</h2>\r\n      <p>Used throughout website Dashboard. Width is fixed at 96px.</p>\r\n\r\n      <DemoSection title=\"Fixed Width Buttons\" :code=\"demoCode\">\r\n        <template #components>\r\n          <div class=\"s-button-container s-button-container--left\">\r\n            <Button\r\n              type=\"button\"\r\n              size=\"fixed-width\"\r\n              variation=\"default\"\r\n              title=\"Default\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              size=\"fixed-width\"\r\n              variation=\"action\"\r\n              title=\"Action\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              size=\"fixed-width\"\r\n              variation=\"warning\"\r\n              title=\"Warning\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              size=\"fixed-width\"\r\n              variation=\"default\"\r\n              title=\"'WithIcon\"\r\n              icon=\"image\"\r\n            />\r\n          </div>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Full-Width Buttons</h2>\r\n      <div class=\"section\">\r\n        <h3>Default</h3>\r\n        <p>Used within cards and panels in the Dashboard.</p>\r\n        <DemoSection title=\"Default\" :code=\"demoCode\">\r\n          <template #components>\r\n            <Button\r\n              size=\"full-width\"\r\n              variation=\"default\"\r\n              title=\"Start Giveaway\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Subscribe</h3>\r\n        <p>Used on the App Platform for Subscribing to Apps.</p>\r\n        <DemoSection title=\"Subscribe\" :code=\"demoCode\">\r\n          <template #components>\r\n            <Button variation=\"subscribe\" title=\"Subscribe\" price=\"$4.99\" />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Paypal</h3>\r\n        <p>Used on the Tip Page for Subscribing to Pro.</p>\r\n        <DemoSection title=\"Paypal\" :code=\"demoCode\">\r\n          <template #components>\r\n            <Button\r\n              variation=\"paypal\"\r\n              title=\"Subscribe with PayPal\"\r\n              price=\"$4.99\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Download</h3>\r\n        <p>Used for Download Streamlabs OBS buttons.</p>\r\n        <DemoSection title=\"Download\" :code=\"demoCode\">\r\n          <template #components>\r\n            <Button\r\n              variation=\"slobs-download\"\r\n              title=\"Download Streamlabs OBS\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n    </div>\r\n    <div class=\"section\">\r\n      <h2>Square Buttons</h2>\r\n      <p>Used for add and subtract number inputs as well as login buttons.</p>\r\n      <DemoSection title=\"Square Buttons\" :code=\"demoCode\">\r\n        <template #components>\r\n          <div class=\"s-button-container s-button-container--left\">\r\n            <Button\r\n              type=\"button\"\r\n              size=\"square\"\r\n              variation=\"default\"\r\n              icon=\"add\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              size=\"square\"\r\n              variation=\"default\"\r\n              icon=\"subtract\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              variation=\"facebook\"\r\n              size=\"square\"\r\n              icon=\"facebook\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              variation=\"periscope\"\r\n              size=\"square\"\r\n              icon=\"periscope\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              variation=\"mixer\"\r\n              size=\"square\"\r\n              icon=\"mixer\"\r\n            />\r\n\r\n            <Button\r\n              type=\"button\"\r\n              variation=\"picarto\"\r\n              size=\"square\"\r\n              icon=\"picarto\"\r\n            />\r\n          </div>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Navigation Buttons</h2>\r\n      <p>Used to bring you between different page states.</p>\r\n\r\n      <DemoSection title=\"Navigation Buttons\" :code=\"demoCode\">\r\n        <template #components>\r\n          <Button\r\n            variation=\"navigation\"\r\n            icon=\"back-alt\"\r\n            title=\"Back to Listings\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Prime Button Text</h2>\r\n      <p>Used for prime button with text.</p>\r\n\r\n      <DemoSection title=\"Prime Button Text\" :code=\"demoCode\">\r\n        <template #components>\r\n          <div class=\"s-button-container s-button-container--left\">\r\n            <Button variation=\"prime-simple\" />\r\n            <Button variation=\"prime-simple\" primeTitle=\"test\" />\r\n          </div>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <table class=\"docs-table\">\r\n      <thead>\r\n        <tr>\r\n          <th>Prop</th>\r\n          <th>Type</th>\r\n          <th>Default</th>\r\n          <th>Description</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr>\r\n          <td>@click</td>\r\n          <td>Function</td>\r\n          <td>null</td>\r\n          <td>\r\n            Emits a click function. If you are using an event modifier such as\r\n            <code>prevent</code>, use <code>native</code>. For example\r\n            <code>@click.native.prevent</code>.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>bgColor</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>Used for background color.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>textColor</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>Used for text color.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>icon</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Use the endings of our custom icons. For example 'icon-settings'\r\n            would be 'settings'. See icons page of the design system for a full\r\n            list.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>iconPosition</td>\r\n          <td>String</td>\r\n          <td>left</td>\r\n          <td>\r\n            Positions the icon left or right. If positioned right, the icon will\r\n            have a left margin of 24px.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>iconImg</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Takes image source string to use an image as an icon. Icon image\r\n            will have a 14px width.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>title</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>Main button text.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>price</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>Used for subscribe buttons in the App Store.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>description</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Used for Streamlabs OBS download buttons to describe the version,\r\n            size and compatibility.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>href</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Used if the the\r\n            <code>type</code> is an <code>a</code> element (links).\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>size</td>\r\n          <td>String</td>\r\n          <td>standard</td>\r\n          <td>\r\n            Size of the button. Options are\r\n            <code>small</code>, <code>large</code>, <code>square</code>,\r\n            <code>fixed-width</code>, and\r\n            <code>full-width</code>\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>state</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            State of the button. Options are\r\n            <code>hover</code>, <code>focus</code>, <code>loading</code> and\r\n            <code>disabled</code>.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>type</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>Set to string 'submit'.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>to</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Used if the the\r\n            <code>type</code> is a <code>router-link</code>. Define the path.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>tag</td>\r\n          <td>String</td>\r\n          <td>button</td>\r\n          <td>\r\n            What type of element the component is. Options are\r\n            <code>button</code>, <code>a</code>,\r\n            <code>router-link</code>\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>target</td>\r\n          <td>String</td>\r\n          <td>_self</td>\r\n          <td>\r\n            Used to set different targets for\r\n            <code>a</code>\r\n            links,\r\n            <code>_self</code>, <code>_blank</code>, <code>_parent</code>,\r\n            <code>_top</code>\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>primeBgColor</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            You can use this prop when you set variation as\r\n            <code>prime</code>. Option is only <code>white</code>.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>primeTitle</td>\r\n          <td>String</td>\r\n          <td>null</td>\r\n          <td>\r\n            Used for prime button text. You can use this prop when you set\r\n            variation as\r\n            <code>prime-simple</code>\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>variation</td>\r\n          <td>String</td>\r\n          <td>default</td>\r\n          <td>\r\n            The variation style of a button. Primary options are\r\n            <code>default</code>, <code>action</code>, <code>prime</code> and\r\n            <code>warning</code>. Other options are <code>subscribe</code>,\r\n            <code>paypal</code>, <code>download</code>, and\r\n            <code>navigation</code>. Reward options are\r\n            <code>rewards-standard</code>, <code>rewards-silver</code>,\r\n            <code>rewards-gold</code>, <code>rewards-platinum</code>,\r\n            <code>rewards-diamond</code>, and <code>rewards-legend</code>.\r\n            Platform options are <code>facebook</code>, <code>mixer</code>,\r\n            <code>twitch</code>, <code>youtube</code>, <code>periscope</code>,\r\n            <code>picarto</code>and <code>paypal-blue</code>.\r\n          </td>\r\n        </tr>\r\n      </tbody>\r\n    </table>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\nimport Accordion from \"./../components/Accordion.vue\";\r\nimport Button from \"./../components/Button.vue\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport ButtonCode from \"!!raw-loader!./Buttons.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    Accordion,\r\n    Button,\r\n    DemoSection\r\n  }\r\n})\r\nexport default class Forms extends Vue {\r\n  isLoading = true;\r\n  isLoadingExample = false;\r\n  demoCode = ButtonCode;\r\n\r\n  buttonClick() {\r\n    alert(\"Button clicked\");\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n.custom-html {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  width: 150px;\r\n}\r\n</style>\r\n";