




















import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Spinner extends Vue {
  @Prop({ default: "text" })
  variation!: String;

  @Prop({ default: "Streamlabs.com" })
  title!: String;

  @Prop({ default: "" })
  subtitle!: String;

  @Prop({ default: false })
  titleSlot!: Boolean;

  @Prop({ default: false })
  hasLink!: Boolean;
}
