export default "<template>\r\n  <div>\r\n    <h1>Sliders</h1>\r\n    <!-- General / Intro -->\r\n    <div class=\"section\">\r\n      <div class=\"row\">\r\n        <p>\r\n          Our slider component is extended from\r\n          <a\r\n            target=\"_blank\"\r\n            href=\"https://github.com/NightCatSama/vue-slider-component\"\r\n            >Vue Slider Component</a\r\n          >\r\n          and makes use of\r\n          <a\r\n            target=\"_blank\"\r\n            href=\"https://github.com/que-etc/resize-observer-polyfill\"\r\n            >Resize Observer Polyfill</a\r\n          >.\r\n        </p>\r\n      </div>\r\n      <pre><code>import { Slider } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  Slider\r\n}</code></pre>\r\n    </div>\r\n\r\n    <!-- Custom Data -->\r\n    <div class=\"section\">\r\n      <h2>Default Slider</h2>\r\n      <DemoSection title=\"Default Slider\" :code=\"demoCode\">\r\n        <template #components>\r\n          <slider\r\n            :value=\"value\"\r\n            @input=\"value => updateValue(value)\"\r\n            :max=\"100\"\r\n            :min=\"1\"\r\n            :interval=\"1\"\r\n            tooltip=\"always\"\r\n            suffix=\"%\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <!-- Simple Theme -->\r\n    <div class=\"section\">\r\n      <div class=\"row\">\r\n        <h2>Simple Theme</h2>\r\n        <p>\r\n          Use in more compact places when you don't want the bright teal\r\n          present.\r\n        </p>\r\n        <DemoSection title=\"Simple Theme\" :code=\"demoCode\">\r\n          <template #components>\r\n            <slider\r\n              :value=\"value\"\r\n              @input=\"value => updateValue(value)\"\r\n              :max=\"100\"\r\n              :min=\"1\"\r\n              :interval=\"1\"\r\n              tooltip=\"always\"\r\n              suffix=\"%\"\r\n              :simpleTheme=\"true\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Props Table -->\r\n    <div class=\"section\">\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Props</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>width</td>\r\n            <td>Number, String</td>\r\n            <td>auto</td>\r\n            <td>The width of the component.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>value</td>\r\n            <td>Number, String, Array</td>\r\n            <td>1</td>\r\n            <td>Initial value of the slider.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>min</td>\r\n            <td>Number</td>\r\n            <td>0</td>\r\n            <td>The minimum numerical value that can be selected</td>\r\n          </tr>\r\n          <tr>\r\n            <td>max</td>\r\n            <td>Number</td>\r\n            <td>100</td>\r\n            <td>The maximum numerical value that can be selected</td>\r\n          </tr>\r\n          <tr>\r\n            <td>interval</td>\r\n            <td>Number</td>\r\n            <td>1</td>\r\n            <td>The steps between the values</td>\r\n          </tr>\r\n          <tr>\r\n            <td>tooltip</td>\r\n            <td>String</td>\r\n            <td>always</td>\r\n            <td>Show the tooltip or not. Options are 'always' or 'false'</td>\r\n          </tr>\r\n          <tr>\r\n            <td>prefix</td>\r\n            <td>String</td>\r\n            <td>\"\"</td>\r\n            <td>\r\n              Prefix label that will be displayed next to the value in the\r\n              tooltip\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>suffix</td>\r\n            <td>String</td>\r\n            <td>\"\"</td>\r\n            <td>\r\n              Suffix label that will be displayed next to the value in the\r\n              tooltip\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>data</td>\r\n            <td>Array</td>\r\n            <td>null</td>\r\n            <td>Custom data.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>disabled</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>Whether to disable the component.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>simpleTheme</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>\r\n              Use for toned down colors to avoid bright teal in small areas.\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n    <!-- <div class=\"section\">\r\n      <div class=\"row\">\r\n        <h1>NEW SLIDER</h1>\r\n        <p>Dont use this yet.</p>\r\n        <div class=\"flex-row\">\r\n          <slider-two\r\n            :value=\"localValue\"\r\n            @input=\"value => updateLocalValue(value)\"\r\n            :min=\"0\"\r\n            :max=\"100\"\r\n            :dataIndexing=\"false\"\r\n            :suffix=\"'%'\"\r\n          />\r\n          <div class=\"icon-holder\"><i class=\"fas fa-question-circle\"></i></div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <slider-two\r\n      :value=\"localValueTwo\"\r\n      @input=\"value => updateLocalValueTwo(value)\"\r\n      :data=\"data\"\r\n      :marks=\"true\"\r\n    /> -->\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\nimport Slider from \"./../components/Slider.vue\";\r\nimport SliderTwo from \"./../components/SliderTwo.vue\";\r\nimport Accordion from \"./../components/Accordion.vue\";\r\nimport SlidersCode from \"!!raw-loader!./Sliders.vue\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\n@Component({\r\n  components: {\r\n    Accordion,\r\n    DemoSection,\r\n    Slider,\r\n    SliderTwo\r\n  }\r\n})\r\nexport default class Sliders extends Vue {\r\n  demoCode = SlidersCode;\r\n  localValue: number | string = 15;\r\n\r\n  updateLocalValue(value) {\r\n    this.localValue = value;\r\n  }\r\n\r\n  localValueTwo: number | string = 15;\r\n\r\n  updateLocalValueTwo(value) {\r\n    this.localValueTwo = value;\r\n  }\r\n\r\n  value = 50;\r\n  data = [\"one\", \"two\", \"three\", \"four\", \"five\", \"six\"];\r\n  updateValue(value) {\r\n    console.log(value); // this function is required, it doesn't have to do anything, it just needs to exist.\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"less\">\r\n@import (reference) \"./../styles/Imports\";\r\n\r\n.flex-row {\r\n  display: flex;\r\n  flex-direction: row;\r\n}\r\n\r\n.icon-holder {\r\n  .margin-left(2);\r\n}\r\n</style>\r\n";