
































import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class VitualItem extends Vue {
  @Prop(String)
  name!: string;

  @Prop(String)
  value!: string;

  @Prop(String)
  preview!: string;

  @Prop(Number)
  quantity!: number;

  @Prop(String)
  rarity!: string;

  @Prop({ default: false })
  selected!: boolean;

  @Prop(String)
  selectionCount!: string;

  @Prop(String)
  remainingTime!: string;

  @Prop({ default: false })
  hasWarning!: boolean;

  @Prop({ default: false })
  isGiveaway!: boolean;

  @Prop(String)
  type!: string;

  isClickable: boolean = false;

  mounted() {
    if (this.$listeners.click) {
      this.isClickable = true;
    }
  }

  get virtualItemClasses() {
    const classes: any = [];

    if (this.rarity) {
      classes.push(`s-virtual-item--${this.rarity}`);
    }

    if (this.selected) {
      classes.push("is-selected");
    }

    if (this.isClickable) {
      classes.push("clickable");
    }

    return classes.join(" ");
  }
}
