






































import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class PaneDropdown extends Vue {
  $refs!: {
    paneMenu: HTMLDivElement;
    paneTitle: HTMLLinkElement;
    panelinks: HTMLSpanElement;
  };

  @Prop()
  icons!: string[];

  @Prop({ default: true })
  dropdownIcon!: boolean;

  @Prop({ default: null })
  menuAlign!: string;

  @Prop({ default: false })
  autoHeight!: boolean;

  @Prop({ default: true })
  closeOnSelect!: boolean;

  @Prop({ default: false })
  custom!: boolean;

  @Prop({ default: false })
  relativeMenu!: boolean;

  @Prop({ default: false })
  simpleMenu!: boolean;

  @Prop({ default: false })
  hoverOption!: boolean;

  paneMenuOpen = false;
  paneList = null;

  created() {
    document.addEventListener("click", this.documentClick);
  }

  mounted() {
    if (!this.custom) {
      let links: any = this.$refs.panelinks;
      let [...list] = links.children;
      this.paneList = list;
    }

    if (this.hoverOption) {
      this.$refs.paneTitle.addEventListener("mouseover", this.show);
      this.$refs.paneMenu.addEventListener("mouseleave", this.hide);
    }
  }

  destroyed() {
    document.removeEventListener("click", this.documentClick);

    if (this.hoverOption) {
      this.$refs.paneTitle.removeEventListener("mouseover", this.show);
      this.$refs.paneMenu.removeEventListener("mouseleave", this.hide);
    }
  }

  get menuClasses() {
    let classes: string[] = [];

    if (this.menuAlign) {
      classes.push(`s-pane-dropdown__menu--${this.menuAlign}`);
    }

    if (this.autoHeight) {
      classes.push("s-pane-dropdown__menu--auto-height");
    }

    if (this.relativeMenu) {
      classes.push("s-pane-dropdown__menu--relative");
    }

    if (this.simpleMenu) {
      classes.push("s-pane-dropdown__menu--simple");
    }

    return classes;
  }

  openContent() {
    this.paneMenuOpen = !this.paneMenuOpen;
  }

  afterOpen(element) {
    element.style.height = "auto";
  }

  open(element) {
    let width = getComputedStyle(element).width;
    element.style.width = width;
    let maxWidth = getComputedStyle(element).width;
    element.style.maxWidth = maxWidth;
    element.style.position = `absolute`;
    element.style.visibility = `hidden`;
    element.style.height = `auto`;
    let height = getComputedStyle(element).height;
    element.style.width = null;
    element.style.position = null;
    element.style.visibility = null;
    element.style.height = 0;
    getComputedStyle(element).height;
    setTimeout(() => {
      element.style.height = height;
    });
  }

  close(element) {
    let height = getComputedStyle(element).height;
    element.style.height = height;
    getComputedStyle(element).height;
    setTimeout(() => {
      element.style.height = 0;
    });
  }

  documentClick(e: Event) {
    let el: any = this.$refs.paneMenu;
    let target = e.target;
    if (el !== target && !el.contains(target)) {
      this.paneMenuOpen = false;
    }
  }

  onMenuClick() {
    this.closeOnSelect ? (this.paneMenuOpen = !this.paneMenuOpen) : null;
  }

  hide() {
    this.paneMenuOpen = false;
  }

  show() {
    this.paneMenuOpen = true;
  }
}
