




























import { Component, Prop, Watch, Vue } from "vue-property-decorator";
import VueSliderComponent from "vue-slider-component";
import ResizeObserver from "resize-observer-polyfill";

@Component({
  components: {
    VueSliderComponent
  }
})
export default class Slider extends Vue {
  $refs!: {
    slider: any;
  };

  @Watch("value")
  updateLocalValue() {
    this.displayValue = this.value;
  }

  @Prop()
  width!: number | string;

  @Prop({ default: 1 })
  value!: number | string | Array<number> | Array<string>;

  @Prop({ default: 0 })
  min!: number;

  @Prop({ default: 100 })
  max!: number;

  @Prop({ default: 1 })
  interval!: number;

  @Prop({ default: "always" })
  tooltip!: "always" | false;

  @Prop({ default: "" })
  prefix!: string;

  @Prop({ default: "" })
  suffix!: string;

  @Prop({ default: false })
  disabled!: boolean;

  @Prop()
  data!: Array<number> | Array<string>;

  @Prop({ default: false })
  simpleTheme!: boolean;

  displayValue: number | string | Array<number> | Array<string> = this.value;
  private debounced: boolean = false;
  private ro = new ResizeObserver((entries, observer) => {
    for (let entry of entries) {
      let { left, top, width, height } = entry.contentRect;
      if (!this.debounced) {
        this.debounce().then(() => {
          if (this.$refs.slider) {
            this.$refs.slider.refresh();
          }
        });
      }
    }
  });

  created() {
    this.$on("input", this.setValue);
  }

  mounted() {
    this.ro.observe(this.$refs.slider.$el);
  }

  beforeDestroy() {
    this.ro.unobserve(this.$refs.slider.$el);
  }

  destroyed() {
    this.$off("input", this.setValue);
  }

  emitInput(val) {
    this.$emit("input", val);
  }

  setValue(val) {
    this.displayValue = val;
  }

  debounce() {
    return new Promise(resolve => {
      if (!this.debounced) {
        this.debounced = true;
        setTimeout(() => {
          this.debounced = false;
          resolve();
        }, 500);
      }
    });
  }
}
