
























import { Component, Prop, Vue } from "vue-property-decorator";
import Badge from "./Badge.vue";
import Button from "./Button.vue";
import EmptySection from "./EmptySection.vue";

@Component({
  components: {
    Badge,
    Button,
    EmptySection
  }
})
export default class PrimeSection extends Vue {
  @Prop({})
  subtitle!: string;

  @Prop({})
  href!: string;

  @Prop()
  onClick!: {
    type: Function;
  };
}
