



























import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class FormGroupV extends Vue {
  @Prop()
  helpText!: string;

  @Prop()
  title!: string;

  @Prop({ default: "" })
  tooltip!: string;

  @Prop({ default: "space-between" })
  titleLayout!: string;

  get titleLayoutStyle() {
    return {
      "justify-content": this.titleLayout
    };
  }
}
