

























import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Notice extends Vue {
  @Prop({ default: "default" })
  bgColor!: {
    type: string;
    required: true;
  };

  @Prop()
  bgIcon!: {
    type: string;
    required: true;
  };

  @Prop()
  title!: {
    type: string;
    required: true;
  };

  @Prop()
  titleColor!: {
    type: string;
    required: true;
  };

  @Prop()
  desc!: {
    type: string;
    required: true;
  };

  @Prop()
  icon!: {
    type: string;
    required: true;
  };
}
