



















import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Guard extends Vue {
  @Prop()
  value?: string;

  @Prop({ default: "Click to show" })
  placeholder!: string;

  @Prop({ default: true })
  showOnClick!: boolean;

  @Prop({ default: "normal" })
  variation!: string;

  @Prop({ default: "text" })
  type!: string;

  visible: boolean = true;

  get prefix() {
    return this.type === "input" ? "s-input-guard" : "s-text-guard";
  }

  showText() {
    if (this.showOnClick) {
      this.visible = false;
    } else {
      this.$emit("click");
    }
  }

  get guardClasses() {
    const classes: string[] = [];
    if (this.visible) {
      classes.push(this.prefix);
    } else {
      classes.push(`${this.prefix}--readable`);
    }

    if (this.variation === "alt") {
      classes.push(`${this.prefix}--alt`);
    }

    return classes;
  }
}
